@import "set";


body {
  font-family: "PT Astra Serif", serif;
  color: #fff;
}

.header {
  position: fixed;
  display: flex;
  justify-content: space-between;
  z-index:5;
  width: ~"calc(100% - 100px)";
  margin:40px 50px;
  &__menu {
    width: 23%;
  }
  &__logo {
    text-align: center;

    &-v {
      font-weight:bold;
      font-size:35px;
      letter-spacing: 8px;
    }
    &-t {
      font-family:Roboto;
      font-weight:300;
      font-size:15px;
      letter-spacing: 21px;
      margin-right:-17px;

    }
  }
  &__tel {
    text-align: right;
    font-family:Roboto;
    font-size:16px;
    line-height: 1.63;
    width: 23%;
  }
}

.section {



  &:first-child {
    background: url(../img/bg1.jpg) no-repeat;
    background-size: cover;
  }

  &_right {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    &.data {
      width:40%;
    }
  }

  &_center {
    //justify-content: center;
    &.data {
      width:80%;
    }
  }

}

.data {
  margin: 0 10%;
  &__title {
    font-size:35px;
    font-weight:bold;
    line-height: 1.23;
  }
  &__text {

    font-family:Roboto;
    font-weight:300;
    font-size:18px;
    line-height: 1.56;
    margin:30px 0;
    width:90%;
  }
  &__button {
    height:45px;
    background: @brown;
    color: black;
    text-decoration: none;
    display: inline-block;
    line-height:45px;
    padding: 0 20px;
    margin-right: 20px;
    font-family:roboto;
    font-size:14px;
    &-group {
      display: flex;
    }
  }

  &__play {
    height:45px;
    width:45px;
    display: inline-block;
    border-radius: 50%;
    border: solid 1px @brown;
    &:hover {
      cursor: pointer;
    }
    &-wrapper {
      //display: inline-block;
    }
    &-text {
      font-family:roboto;
      font-size:14px;
      display: inline-block;
      //width: 80%;
      vertical-align: middle;
      margin-left:5px;
    }

    i {
      color: @brown;
      margin-top:14px;
      margin-left:18px;
    }
  }

  &__bullets {
    display: flex;
    justify-content: space-around;
  }
}


// Navigation begin

#pp-nav .active span{
  background: @brown !important;
  border: 1px solid @brown !important;
}

#pp-nav span {
  border-radius: unset;
  transform: rotate(135deg);

}

